import React from 'react';
import classnames from 'classnames';
import {Link as Href, Router} from 'react-router-dom';
import {Link, DirectLink, Element, Events, animateScroll as scroll, scrollSpy, scroller} from 'react-scroll';
class Menu extends React.Component {
    /**
     * @constructor
     * Method for initialising data
     */
    constructor(props){
        super(props);

        this.state = {
            category: this.props.category,
            menuItems: this.props.menuItems || []
        };
    }

    shouldComponentUpdate(nextProps) {
        if (nextProps.menuItems !==this.state.menuItems) {
            this.setState({menuItems: nextProps.menuItems});

            return true;
        }

        return false;
    }

    componentDidUpdate() {
        if (this.props.category) {
            //TODO fix scroll
        }
    }

    renderMenuItem(item, i, isActive) {
        return <Link
            key={'menu' + i}
            onClick={()=>{
                if (item.onClick) {
                    item.onClick();
                } else {
                    this.props.menuItemClick(i);
                }
            }}
            activeClass='menu__item_active'
            className={'menu__item' + (item.cssClass ? (' ' + item.cssClass) : '')}
            to={'section' + item.id}
            isDynamic={true}
            offset={-80}
            spy={true}
            smooth={true}
            duration={500} >
            {item.title}
        </Link>;
    }

    renderLink(item, i) {
        return <a key={'link' + i} href={item.link} className={'menu__item'}>{item.title}</a>;
    }

    /**
     * Main method for render this component
     */
    render(){
        let menuItems = [];

        menuItems.push(this.state.menuItems.map((item, i)=>{
            return item.link ? this.renderLink(item, i) : this.renderMenuItem(item, i, this.state.menuItems[i].active);
        }));

        return (
            <nav className='menu'>
                {menuItems.map((item)=>{
                    return item;
                })}
            </nav>
        );
    }
}

export default Menu;
