import React from 'react';
import Button from '../ButtonComponent';
import BonusIcon from '../icons/BonusIcon';

class MobileAuthArea extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            profile: this.props.profile
        }
    }

    get enterButton() {
        let title = 'Войдите в свой аккаунт';

        return (<Button
            buttonType="default"
            buttonTitle={title}
            buttonClass="btn mobile-auth-btn"
            onClickMethod={this.props.onShowUserProfile} >
            {title}
        </Button>);
    }

    get profileInfo() {
        return (<a className="inf-block-link" href="#/cabinet" onClick={this.props.onShowUserProfile}>
            <div className='inf-block'>
                <div className="mobile-inf">{this.state.profile.firstname}</div>
                <div className="mobile-inf">+{this.state.profile.phone}</div>
                <div className="mobile-inf mobile-bonuses">Побо-бонусов
                    <span className="mobile-inf mobile-bonuses-count">{this.state.profile.bonuses}</span>
                    <BonusIcon/>
                </div>
            </div>
        </a>);
    }

    render() {
        let title = 'Войти';

        return ( <main className="page mobile-page">
            <div className="container">
                {this.props.profile.phone ? this.profileInfo : this.enterButton}
            </div>
        </main>);
    }
}

export default MobileAuthArea;
