import React from "react";
import classNames from "classnames";
import { YMInitializer } from "react-yandex-metrika";
import ReactPixel from "react-facebook-pixel";
import Menu from "./MenuComponent";
import InstagramIcon from "./icons/InstagramIcon";
import FacebookIcon from "./icons/FacebookIcon";


class Footer extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            companyPhone: this.props.companyPhone,
            socialLinks: this.props.socialLinks,
            authorized: this.props.authorized,
            showMobileAuth: this.props.showMobileAuth,
        };
    }

    componentDidMount() {
        const options = {
            autoConfig: true,
            // debug: true,
        };
        ReactPixel.init("320228355308484", null, options);

        ReactPixel.pageView();
    }

    shouldComponentUpdate(nextProps, nextState) {
        if (nextProps.authorized !== this.state.authorized) {
            this.setState({ authorized: nextProps.authorized, });

            return true;
        } if (nextProps.socialLinks !== this.state.socialLinks) {
            this.setState({ socialLinks: nextProps.socialLinks, });

            return true;
        } if (nextProps.companyPhone !== this.state.companyPhone) {
            this.setState({ companyPhone: nextProps.companyPhone, });

            return true;
        } if (nextProps.showMobileAuth !== this.state.showMobileAuth) {
            this.setState({ showMobileAuth: nextProps.showMobileAuth, });

            return true;
        }

        return false;
    }

    /**
     * Main method for render this component
     */
    render() {
        const menuItems = [
            {
                title: "О нас",
                onClick: () => this.props.onNavigateRequest("#/about", true),
            },
            {
                title: "Акции",
                onClick: () => this.props.onNavigateRequest("#/sales", true),
            },
            {
                title: "Рестораны",
                onClick: () => this.props.onNavigateRequest("#/restaurants", true),
            }
        ];

        if (!this.state.showMobileAuth) {
            menuItems.push({
                link: "#/legal",
                title: "Правовая информация",
            });
        }

        return (
            <footer className={classNames("footer", { "mobile-page": this.state.showMobileAuth, })}>
                <div className="container">
                    <div className="footer__content">
                        <div className="top-level">
                            <div className="left-bar">
                                <Menu
                                    menuItems={menuItems}
                                />
                            </div>
                            <div className="right-bar">
                                <div className="contact-number"><a href={`tel:${this.state.companyPhone}`}>{this.state.companyPhone}</a></div>
                            </div>
                        </div>
                        <div className="bottom-level">
                            <div className="left-bar">
                                <div className="text-description copyright">2019 pobo. Все права защищены</div>
                            </div>
                            <div className="right-bar">
                                <div className="social-icons">
                                    <a href={this.state.socialLinks.instagram} className="social-icons__item" target="_blank">
                                        <InstagramIcon />
                                    </a>
                                    <a href={this.state.socialLinks.facebook} className="social-icons__item" target="_blank">
                                        <FacebookIcon />
                                    </a>
                                </div>
                                <div className="tag">#{this.state.socialLinks.hashtag}</div>
                            </div>
                        </div>
                    </div>
                </div>
                <YMInitializer accounts={[54294081]} options={{ webvisor: true, }} version="2" />
            </footer>
        );
    }
}

export default Footer;
